<template>
  <div class="description-tester">
    <h1>商品描述生成测试</h1>
    
    <el-card class="api-settings-card" v-if="!hideSettings">
      <template #header>
        <div class="card-header">
          <span>Coze API 设置</span>
          <el-button type="text" @click="hideSettings = true">隐藏</el-button>
        </div>
      </template>
      
      <div v-if="useStoredSettings">
        <el-alert
          type="success"
          show-icon
          :closable="false"
        >
          <p>当前使用设置页面保存的API配置</p>
          <p v-if="cozeSettings.cozeApiToken">API令牌: {{ maskToken(cozeSettings.cozeApiToken) }}</p>
          <p v-if="cozeSettings.cozeWorkflowId">工作流ID: {{ cozeSettings.cozeWorkflowId }}</p>
          <p v-if="cozeSettings.cozeBotId">智能体ID: {{ cozeSettings.cozeBotId }}</p>
        </el-alert>
        <div class="settings-actions">
          <el-button type="primary" @click="testConnection">测试连接</el-button>
          <el-button @click="useStoredSettings = false">使用自定义设置</el-button>
          <el-button @click="goToSettings">去设置页管理</el-button>
        </div>
      </div>
      
      <el-form v-else :model="customSettings" label-position="top">
        <el-form-item label="Coze API 令牌">
          <el-input v-model="customSettings.cozeApiToken" placeholder="请输入Coze API令牌" />
        </el-form-item>
        <el-form-item label="工作流ID">
          <el-input v-model="customSettings.cozeWorkflowId" placeholder="请输入工作流ID" />
        </el-form-item>
        <el-form-item label="智能体ID (可选)">
          <el-input v-model="customSettings.cozeBotId" placeholder="请输入智能体ID (可选)" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="testCustomConnection">测试连接</el-button>
          <el-button @click="useStoredSettings = true">使用全局设置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    
    <el-button v-if="hideSettings" type="text" @click="hideSettings = false" class="show-settings-btn">
      显示API设置
    </el-button>

    <el-divider content-position="center">API调用配置</el-divider>

    <el-alert
      type="info"
      show-icon
      :closable="false"
      style="margin-bottom: 20px;"
    >
      <p>由于CozeAPI接口调用可能超时，建议开启异步模式</p>
    </el-alert>

    <el-card class="settings-card">
      <el-form-item label="默认使用异步模式" label-width="150px">
        <el-switch v-model="defaultUseAsync" @change="saveDefaultSettings" />
        <span class="setting-description">开启后会使用异步API调用方式，避免超时问题</span>
      </el-form-item>
    </el-card>

    <el-divider content-position="center">商品描述生成</el-divider>
    
    <el-form :model="testForm" class="test-form">
      <el-form-item label="测试关键词">
        <el-input 
          v-model="testForm.keywords" 
          placeholder="请输入要测试的商品关键词，多个关键词用逗号分隔" 
          type="text"
        />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="quickTest">快速测试</el-button>
        <el-button @click="clearKeywords">清空</el-button>
      </el-form-item>
    </el-form>
    
    <div class="main-content">
      <div class="generator-container">
        <DescriptionGenerator 
          ref="generatorRef"
          @use-description="handleUseDescription" 
          :apiSettings="getActiveSettings()"
          :initialKeywords="testForm.keywords" 
        />
      </div>
      
      <el-divider direction="vertical" />
      
      <div class="preview-container">
        <h2>描述预览</h2>
        <el-card class="preview-card" v-if="currentDescription">
          <div class="preview-text">{{ currentDescription }}</div>
          <div class="preview-actions">
            <el-button size="small" type="primary" @click="copyToClipboard">复制描述</el-button>
            <el-button size="small" @click="clearPreview">清除</el-button>
          </div>
        </el-card>
        <el-empty v-else description="还没有生成的描述" />
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
import { ElMessage } from 'element-plus';
import { useRouter } from 'vue-router';
import DescriptionGenerator from '../components/DescriptionGenerator.vue';
import axios from 'axios';

export default {
  name: 'DescriptionTester',
  components: {
    DescriptionGenerator
  },
  setup() {
    const router = useRouter();
    const cozeSettings = ref({
      cozeApiToken: '',
      cozeWorkflowId: '',
      cozeBotId: ''
    });
    
    const customSettings = ref({
      cozeApiToken: '',
      cozeWorkflowId: '',
      cozeBotId: ''
    });
    
    const defaultUseAsync = ref(true);
    const useStoredSettings = ref(true);
    const hideSettings = ref(false);
    const currentDescription = ref('');
    
    // 测试表单
    const testForm = ref({
      keywords: ''
    });
    
    // 引用DescriptionGenerator组件
    const generatorRef = ref(null);
    
    // 从本地存储加载设置
    const loadSettings = () => {
      const savedSettings = localStorage.getItem('apiSettings');
      if (savedSettings) {
        try {
          const parsed = JSON.parse(savedSettings);
          cozeSettings.value = {
            ...cozeSettings.value,
            ...parsed
          };
          
          // 如果没有设置API令牌或工作流ID，默认切换到自定义设置
          if (!cozeSettings.value.cozeApiToken || !cozeSettings.value.cozeWorkflowId) {
            useStoredSettings.value = false;
          }
        } catch (error) {
          console.error('加载设置失败:', error);
          useStoredSettings.value = false;
        }
      } else {
        // 如果没有保存的设置，默认切换到自定义设置
        useStoredSettings.value = false;
      }

      // 加载异步设置
      try {
        const savedAsyncSetting = localStorage.getItem('defaultUseAsync');
        if (savedAsyncSetting !== null) {
          defaultUseAsync.value = JSON.parse(savedAsyncSetting);
        }
      } catch (error) {
        console.error('加载异步设置失败:', error);
      }
    };
    
    // 保存默认设置
    const saveDefaultSettings = () => {
      localStorage.setItem('defaultUseAsync', JSON.stringify(defaultUseAsync.value));
      ElMessage.success('设置已保存');
    };
    
    // 获取当前活动的设置
    const getActiveSettings = () => {
      const settings = useStoredSettings.value ? cozeSettings.value : customSettings.value;
      // 添加异步模式设置
      return {
        ...settings,
        useAsyncMode: defaultUseAsync.value
      };
    };
    
    // 掩码显示令牌
    const maskToken = (token) => {
      if (!token) return '';
      if (token.length <= 10) return token;
      return token.substring(0, 5) + '...' + token.substring(token.length - 5);
    };
    
    // 测试连接（使用全局设置）
    const testConnection = async () => {
      if (!cozeSettings.value.cozeApiToken || !cozeSettings.value.cozeWorkflowId) {
        ElMessage.warning('全局设置中缺少API令牌或工作流ID，请先在设置页面完成配置');
        return;
      }
      
      try {
        const response = await axios.post('/api/test-coze-api', {
          accessToken: cozeSettings.value.cozeApiToken,
          workflowId: cozeSettings.value.cozeWorkflowId,
          botId: cozeSettings.value.cozeBotId || undefined
        });
        
        if (response.data.success) {
          ElMessage.success('连接测试成功');
        } else {
          ElMessage.error(`连接测试失败: ${response.data.message}`);
        }
      } catch (error) {
        console.error('API测试失败:', error);
        ElMessage.error(error.response?.data?.message || '连接测试失败');
      }
    };
    
    // 测试连接（使用自定义设置）
    const testCustomConnection = async () => {
      if (!customSettings.value.cozeApiToken || !customSettings.value.cozeWorkflowId) {
        ElMessage.warning('请输入API令牌和工作流ID');
        return;
      }
      
      try {
        const response = await axios.post('/api/test-coze-api', {
          accessToken: customSettings.value.cozeApiToken,
          workflowId: customSettings.value.cozeWorkflowId,
          botId: customSettings.value.cozeBotId || undefined
        });
        
        if (response.data.success) {
          ElMessage.success('连接测试成功');
        } else {
          ElMessage.error(`连接测试失败: ${response.data.message}`);
        }
      } catch (error) {
        console.error('API测试失败:', error);
        ElMessage.error(error.response?.data?.message || '连接测试失败');
      }
    };
    
    // 跳转到设置页面
    const goToSettings = () => {
      router.push('/settings');
    };
    
    // 使用生成的描述
    const handleUseDescription = (description) => {
      currentDescription.value = description;
    };
    
    // 复制到剪贴板
    const copyToClipboard = async () => {
      try {
        await navigator.clipboard.writeText(currentDescription.value);
        ElMessage.success('描述已复制到剪贴板');
      } catch (error) {
        console.error('复制失败:', error);
        ElMessage.error('复制失败，请手动复制');
      }
    };
    
    // 清除预览
    const clearPreview = () => {
      currentDescription.value = '';
    };
    
    // 快速测试
    const quickTest = async () => {
      if (!testForm.value.keywords) {
        ElMessage.warning('请先输入测试关键词');
        return;
      }
      
      // 调用DescriptionGenerator组件的生成方法
      if (generatorRef.value && typeof generatorRef.value.generateDescription === 'function') {
        await generatorRef.value.generateDescription();
      } else {
        // 如果不能直接调用，模拟点击生成按钮
        const generateBtn = document.querySelector('.description-generator .el-button');
        if (generateBtn) {
          generateBtn.click();
        } else {
          ElMessage.error('无法触发描述生成');
        }
      }
    };
    
    // 清空关键词
    const clearKeywords = () => {
      testForm.value.keywords = '';
    };
    
    // 加载保存的设置
    loadSettings();
    
    return {
      cozeSettings,
      customSettings,
      defaultUseAsync,
      useStoredSettings,
      hideSettings,
      currentDescription,
      testForm,
      testConnection,
      testCustomConnection,
      handleUseDescription,
      copyToClipboard,
      clearPreview,
      getActiveSettings,
      maskToken,
      goToSettings,
      saveDefaultSettings,
      quickTest,
      clearKeywords,
      generatorRef
    };
  }
};
</script>

