  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.generator-container, .preview-container {
  flex: 1;
}

.preview-card {
  margin-top: 10px;
}

.preview-text {
  white-space: pre-line;
  font-size: 14px;
  line-height: 1.6;
  margin-bottom: 15px;
  max-height: 400px;
  overflow-y: auto;
}

