<template>
  <div class="description-generator">
    <el-form ref="form" :model="form" label-position="top">
      <el-form-item label="商品关键词" prop="keywords">
        <el-input
          v-model="form.keywords"
          placeholder="输入关键词，多个关键词用逗号分隔"
          type="textarea"
          :rows="2"
        />
      </el-form-item>
      
      <el-form-item>
        <el-button 
          type="primary" 
          @click="generateDescription" 
          :loading="loading"
        >
          生成描述
        </el-button>
      </el-form-item>
    </el-form>
    
    <div v-if="description" class="generated-description">
      <div class="description-header">
        <h3>生成的商品描述</h3>
        <div>
          <el-button type="primary" size="small" @click="useDescription">
            使用此描述
          </el-button>
          <el-button size="small" @click="regenerate">
            重新生成
          </el-button>
        </div>
      </div>
      
      <el-card class="description-content">
        <div class="description-text">{{ description }}</div>
      </el-card>
    </div>
  </div>
</template>

<script>
import { ref, reactive, watch } from 'vue';
import { ElMessage } from 'element-plus';
import { descriptionAPI } from '../utils/api';

export default {
  name: 'DescriptionGenerator',
  props: {
    apiSettings: {
      type: Object,
      default: () => ({})
    },
    initialKeywords: {
      type: String,
      default: ''
    }
  },
  emits: ['use-description'],
  setup(props, { emit }) {
    const form = reactive({
      keywords: props.initialKeywords || ''
    });
    
    // 监听initialKeywords变化
    watch(() => props.initialKeywords, (newValue) => {
      if (newValue !== undefined) {
        form.keywords = newValue;
      }
    });
    
    const loading = ref(false);
    const description = ref('');
    
    // 生成商品描述
    const generateDescription = async () => {
      if (!form.keywords.trim()) {
        ElMessage.warning('请输入商品关键词');
        return;
      }
      
      try {
        loading.value = true;
        
        // 准备API配置
        const config = {};
        
        // 如果提供了API设置，添加到请求中
        if (props.apiSettings && Object.keys(props.apiSettings).length > 0) {
          config.headers = {
            'X-Coze-Settings': JSON.stringify(props.apiSettings)
          };
        }
        
        const response = await descriptionAPI.generateDescription(form.keywords, config);
        description.value = response.data.data.description;
        ElMessage.success('描述生成成功');
      } catch (error) {
        console.error('生成描述失败:', error);
        ElMessage.error(error.response?.data?.message || '生成描述失败，请重试');
      } finally {
        loading.value = false;
      }
    };
    
    // 重新生成
    const regenerate = () => {
      generateDescription();
    };
    
    // 使用此描述
    const useDescription = () => {
      emit('use-description', description.value);
    };
    
    return {
      form,
      loading,
      description,
      generateDescription,
      regenerate,
      useDescription
    };
  }
};
</script>

<style scoped>
.description-generator {
  margin-bottom: 20px;
}

.generated-description {
  margin-top: 20px;
}

.description-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.description-header h3 {
  margin: 0;
  font-size: 16px;
}

.description-content {
  margin-bottom: 20px;
}

.description-text {
  white-space: pre-line;
  font-size: 14px;
  line-height: 1.6;
}
</style> 